/* https://www.w3schools.com/css/css3_variables.asp */
:root {
    --eggplantMedium: #BA9CAB;
    --eggplant: #614051;
    --eggplantLight: #e3ccd8;
    --salmon: #e5989b;
    --salmonLight: #f9dedf;
    --melon: #ffb4a2;
    --melonLight: #fee2db;
    --spearmint: #9bb4a8;
    --spearmintLight: #d1e5dc;
    font-family: "Inter", sans-serif;
}

body {
    min-height: 100%;
    padding-bottom: 80px;
}

.footerContainer {
    background-color: var(--eggplant);
    height: 50px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
}

.footerText {
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 130px;
    color: var(--melonLight);
}

.slogans {
    text-align: center;
}

/* Big screens */
@media (min-width: 1551px) {

    /* LogIn and Create User buttons */
    #logIn,
    #createUser {
        color: var(--eggplant);
        font-size: 15px;
        font-weight: bold;
        float: right;
        margin-right: 60px;
    }
}

/* Laptops */
@media (min-width: 1025px) and (max-width: 1550px) {

    /* LogIn and Create User buttons */
    #logIn,
    #createUser {
        color: var(--eggplant);
        font-size: 15px;
        font-weight: bold;
        float: right;
        margin-right: 60px;
    }
}

/* iPads, Tablets */
@media (min-width: 481px) and (max-width: 1024px) {

    /* LogIn and Create User buttons */
    #logIn,
    #createUser {
        color: var(--eggplant);
        font-size: 15px;
        font-weight: bold;
        float: right;
        margin-right: 60px;
    }
}

/* Mobile */
@media (max-width: 480px) {}

.searchbar {
    width: 597px;
    height: 64px;
    box-shadow: 0px 5px 8px rgb(185, 185, 185);
    padding-right: 4%;
    border-radius: 8px;
}

.searchdiv {
    position: relative;
    margin-bottom: 120px;
    top: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.searchbar input::placeholder {
    color: rgb(184, 182, 182);
    font-weight: bold;
    opacity: 1;
}

.searchicon {
    cursor: pointer;
    color: rgb(184, 182, 182);
    padding-left: 30px;
    font-size: 40px;
}


.sortbutton {
    background-color: var(--spearmintLight);
    cursor: pointer;
border-radius: 12px;
width: 150px;
height: 40px;
margin-right: 15px;
font-size: 18px;
box-shadow: 0px 5px 8px rgb(185, 185, 185);
border: 2px solid transparent;
opacity: 0.8; 
}


.sortdiv {
    color: var(--spearmint);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 21px;
    position: sticky;
    top: 70px;
    
}

.recipe-container {
        padding-top: 100px; /* increase padding to make room for sortdiv */
        margin-top: -80px; /* create negative margin to pull content up */
  }